.CodeMirror{font-family:"Source Code Pro",monospace;background:none;position:absolute;box-sizing:border-box;border:1px solid #ccc;border-radius:4px;box-shadow:inset 0 1px 1px rgba(0,0,0,.075);box-shadow:none}.CodeMirror div.CodeMirror-cursor{pointer-events:none;border-left:1px solid #101112}.CodeMirror-selected{background:rgba(170,170,170,.55)}.CodeMirror-focused .CodeMirror-selected{background:rgba(140,150,255,.8)}.editor.multiline .CodeMirror-line:not(:last-child)>span:after{pointer-events:none;color:rgba(127,127,127,.33);content:"¬"}.CodeMirror-line .cm-space::before,.CodeMirror-line .cm-special::before{color:rgba(127,127,127,.33);content:"•";position:absolute}.CodeMirror-line .cm-special::before{color:#d22}.CodeMirror-line .cm-tab{background:url(data:image/png;base64,);background-position:100%;background-repeat:no-repeat}